[data-dpr="2"] .f14 { font-size: 28px; /*no*/ }

[data-dpr="3"] .f14 { font-size: 42px; /*px*/ }

.f16 { font-size: 16px; }

[data-dpr="2"] .f16 { font-size: 32px; /*no*/ }

[data-dpr="3"] .f16 { font-size: 48px; /*px*/ }

.f18 { font-size: 18px; }

[data-dpr="2"] .f18 { font-size: 36px; /*no*/ }

[data-dpr="3"] .f18 { font-size: 54px; /*px*/ }

.f20 { font-size: 20px; }

[data-dpr="2"] .f20 { font-size: 40px; /*no*/ }

[data-dpr="3"] .f20 { font-size: 60px; /*px*/ }

.f22 { font-size: 22px; }

[data-dpr="2"] .f22 { font-size: 44px; /*no*/ }

[data-dpr="3"] .f22 { font-size: 66px; /*px*/ }

.f24 { font-size: 24px; }

